@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-elem {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    color: #DDDDDD !important;
    font-weight: 700;
}

.nav-elem:hover {
    color: #AAAAAA !important;
}
.-card {
    position: relative;
    width: 350px !important;
    text-align: center;
    height: 350px !important;
    display: block;
    padding-top: 100%;
    text-align: center;
    margin:auto;
}

.card-image {
    position: absolute;
    border-radius: 3px 3px;
    display:block;
    height: 350px !important;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: cover;
    background-color: gainsboro;
    /* overflow: hidden; */
}

.card-overlay {
    opacity: 0.5;
    border-radius: 3px 3px;
    position: absolute;
    padding: 10 10;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #212529;
    overflow: hidden;
    width: 100%;
    height: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    text-align: center;
}

.card-overlay:hover {
    opacity: 0.9;
}

.-card:hover .card-overlay {
    border-radius: 20 20;
    height: 100%;
}

.card-text {
    border-radius: 20 20;
    padding: 20px;
    color: white;
    text-align: center;
}

.card-row {
    text-align: center;
    padding: 0 10% 10% 0;
}

.column, .Col-link {
    text-align: center;
    height: 350px;
